import React from 'react'
import Footer from '../../footer/Footer'
import Navbar from '../../navbar/Navbar'
import Grid from './components/Grid'
import Imagen from './components/Imagen'
import './css/quienes.css'

const Quienes = () => {
    return (

        <>
            <header>
                <Navbar/>
            </header>
            
            <main>
                <section>
                    <Imagen/>
                </section>
                <hr />
                <section>
                    <Grid />  
                </section> 
            </main>
            <Footer/>
            
        </>

        
    )
}

export default Quienes
