import React from 'react'
import '../css/galeria.css'

const Galeria = () => {

   

    return (
        <>
            <section className="gallery min-vh-100">
          <div className="container-lg">
             <div className="row gy-4 row-cols-1 row-cols-sm-2 row-cols-md-3">
                <div className="col" data-bs-target="#gallery-modal">
                  <img src="img/certificado_pagina10001.jpg" className="gallery-item" alt="gallery"/>
                </div>
                <div className="col">
                   <img src="img/certificado_pagina10002.jpg" className="gallery-item" alt="gallery" />
                </div>
                <div className="col">
                   <img src="img/certificado_pagina10003.jpg" className="gallery-item" alt="gallery"/>
                </div>
                <div className="col">
                   <img src="img/certificado_pagina70001.jpg" className="gallery-item" alt="gallery"/>
                </div>
                <div className="col">
                   <img src="img/certificadp_pagina40001.jpg" className="gallery-item" alt="gallery"/>
                </div>
                
             </div>
          </div>
          
       </section>

       <div className="modal fade" id="gallery-modal"  aria-labelledby="exampleModal" aria-hidden="true">
       <div className="modal-dialog modal-dialog-centered modal-lg">
         <div className="modal-content">
           <div className="modal-header">
             
             <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
           </div>
           <div className="modal-body">
              <img src="img/1.jpg" className="modal-img" alt="modal img"/>
           </div>
         </div>
       </div>
     </div>
        </>
    )
}

export default Galeria
