import React from 'react'
import '../css/quienes.css'

const Imagen = () => {
    return (
        <>
            <section id="hero">
                <div className="container2">
                    <div className="info">
                        <h1>Beta Systems Ltda</h1>
                        <p>Somos una empresa de servicios informáticos, con más de 15 de experiencia en la industria, 
                            contamos con un equipo de profesionales altamente capacitados, con una amplia experiencia en
                            el sector de tecnologia, ofreciendo soluciones de infraestructura e informatica a medida, 
                            orientados a pequeñas, medianas y grandes empresas.
                        </p>
                        
                    </div>
                </div>
            </section>
    
        </>
    )
}

export default Imagen
